import Vue from 'vue'
import VueRouter from 'vue-router'
import Derivatives from '../views/Derivatives.vue'
import articles from "@/assets/articles.json";

Vue.use(VueRouter)

const routes = [{
    path: '/',
    redirect: {
      name: 'Derivatives'
    }
  },
  {
    path: '/derivate',
    name: 'Derivatives',
    component: Derivatives,
    meta: {
      isListPage: true
    }
  },
  {
    path: "*",
    component: () => import( /* webpackChunkName: "404" */ `../views/NotFound.vue`),
  }
]

// Add routes for articles
for (let article of articles) {
  routes.push({
    path: '/derivate/' + article.id,
    component: () => import( /* webpackChunkName: "derivative" */ `../views/DerivativesArticles/Article${article.id}.vue`),
    props: {
      id: article.id,
      title: article.title
    }
  });
}

const router = new VueRouter({
  // mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router