<template>
  <v-card class="MCard elevation-0" id="MCard" :to="goTo" :ripple="false">
    <v-card-title id="title" class="center-text">
      <slot name="title"></slot>
    </v-card-title>

    <v-card-text id="description" class="text-center">
      <slot></slot>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  name: "MCard",
  props: {
    articleId: {
      type: Number,
      default: null
    }
  },
  computed: {
    goTo() {
      return this.$route.path.replace(/\/$/, "") + "/" + this.articleId;
    }
  }
};
</script>

<style scoped>
.MCard * {
  word-break: keep-all !important;
}

#MCard {
  margin: 0.2em 0em;
  border-radius: 3px !important;
  border-bottom-right-radius: 15px !important;
  /* box-shadow: 1px 1px 15px rgba(67, 38, 100, 0.15) !important; */

  background: #1a1d24
    linear-gradient(135deg, #41b883 10px, rgba(0, 0, 0, 0.01) 0);

  align-items: center !important;
  justify-content: center !important;

  transition: all 0.2s;
  animation: none;
}

.v-card--link:focus:before {
  opacity: 0 !important;
}

#title,
#description {
  font-family: Recursive, "Menlo", "DejaVu Sans Mono",
    "Bitstream Vera Sans Mono", Courier, monospace !important;
  font-variation-settings: "MONO" 1 !important;
  font-weight: 450;
}

#title {
  margin-bottom: 1rem !important;
  line-height: 1.2 !important;

  font-size: 1.4em;
  color: #dfa612;

  text-align: center !important;
  display: block;
}

#description {
  color: antiquewhite;
  margin-bottom: 0;
  font-size: 1rem;
  text-align: center;
}

@media only screen and (min-width: 70em) {
  #MCard:hover {
    transform: translateY(-2px);
  }
}
</style>