<template>
  <v-container class="MBannerBackground pa-0" fluid>
    <v-sparkline
      :value="value"
      :gradient="gradient"
      :smooth="radius || false"
      :padding="padding"
      :line-width="width"
      :stroke-linecap="lineCap"
      :gradient-direction="gradientDirection"
      :fill="fill"
      :type="type"
      :auto-line-width="autoLineWidth"
      height="45%"
      auto-draw
    ></v-sparkline>
  </v-container>
</template>

<script>
const gradients = [
  ["#222"],
  ["#42b3f4"],
  ["red", "orange", "yellow"],
  ["purple", "violet"],
  ["#00c6ff", "#F0F", "#FF0"],
  ["#f72047", "#ffd200", "#1feaea"]
];

export default {
  name: "MBannerBackground",
  data: () => ({
    width: 2,
    radius: 10,
    padding: 8,
    lineCap: "round",
    gradient: gradients[5],
    value: [11, 2, 5, 9, 5, 3, 9, 3, 5, 0, 0, 3, 8, 3, 11],
    gradientDirection: "top",
    gradients,
    fill: false,
    type: "trend",
    autoLineWidth: false
  })
};
</script>

<style scoped>
.MBannerBackground {
  position: absolute !important;
  top: -0.6em !important;
  left: 0 !important;

  z-index: 0 !important;
}

@media only screen and (min-width: 600px) {
  .MBannerBackground {
    top: -1.5em !important;
  }
}

@media only screen and (min-width: 960px) {
  .MBannerBackground {
    top: -2em !important;
  }
}

@media only screen and (min-width: 1264px) {
  .MBannerBackground {
    top: -3.5em !important;
  }
}
</style>